import React from 'react';

import {Route, Switch} from 'react-router-dom';

import About from './about';
import Test from './test';
import About_Micaella from './about_micaella';
import About_Maria from './about_maria';
import About_Cyang from './about_chunzheng';
import About_Katie from './about_katie';
import About_Anna from './about_anna';


export default class App extends React.Component {
  state = {
    data: null
  };

  componentDidMount() {
    // Call our fetch function below once the component mounts
    this.callBackendAPI()
      .then(res => this.setState({ data: res.express }))
      .catch(err => console.log(err));
  }
    // Fetches our GET route from the Express server. (Note the route we are fetching matches the GET route from server.js
  callBackendAPI = async () => {
    const response = await fetch('/express_backend');
    const body = await response.json();

    if (response.status !== 200) {
      throw Error(body.message)
    }
    return body;
  };

  render() {return (
    <Switch>
      <Route exact path="/" component={About} />
      <Route path="/about" component={About} />
      <Route path="/test" component={Test} />

      <Route path="/about_micaella" component={About_Micaella} />
      <Route path="/about_maria" component={About_Maria} />
      <Route path="/about_chunzheng" component={About_Cyang} />
      <Route path="/about_katie" component={About_Katie} />
      <Route path="/about_anna" component={About_Anna} />

    </Switch>
  )}
}
