import React from 'react'
import Navbar from "react-bootstrap/Navbar";
import "bootstrap/dist/css/bootstrap.min.css";

import SideNav, {NavItem, NavIcon, NavText } from '@trendmicro/react-sidenav';
import "@trendmicro/react-sidenav/dist/react-sidenav.css";

import {Link} from 'react-router-dom';

import "./styles.css";

export default class Test extends React.Component {
    render() {
        return (
            <body>
                <div class="sticky-top">
                    <Navbar variant="dark" sticky="top">
                    <Navbar.Brand>RefrigeratorOrganizer</Navbar.Brand>
                    </Navbar>
                </div>  
                <div>
                    <div class="sidenav">
                        <SideNav>
                            <SideNav.Toggle />
                            <SideNav.Nav defaultSelected="test">
                                <NavItem eventKey="about">
                                    <NavIcon>
                                        <Link to="/about"><i class="glyphicon glyphicon-info-sign"></i></Link>
                                    </NavIcon>
                                    <NavText><Link to="/about"> About </Link></NavText>
                                </NavItem>
                                <NavItem eventKey="test">
                                    <NavIcon> 
                                        <Link to="/test"><i class="glyphicon glyphicon-asterisk"></i></Link>
                                    </NavIcon>
                                    <NavText><Link to="/test"> Test </Link></NavText>
                                </NavItem>
                            </SideNav.Nav>
                        </SideNav>
                    </div>
                    <div class="content">
                        TEST
                        
                    </div>
                </div>
                

                
            </body>
        );
    }

}
