import React from 'react';
import Navbar from "react-bootstrap/Navbar";
import Image from "react-bootstrap/Image";
import "bootstrap/dist/css/bootstrap.min.css";

import SideNav, {NavItem, NavIcon, NavText } from '@trendmicro/react-sidenav';
import "@trendmicro/react-sidenav/dist/react-sidenav.css";

import {Link} from 'react-router-dom';

import "./styles.css";
import img_cy from "./images/cyang_img.jpg"

export default class About_Cyang extends React.Component {
    render() {
        return (
            <body>
            <div class="sticky-top">
                <Navbar variant="dark" sticky="top">
                    <Navbar.Brand>RefrigeratorOrganizer</Navbar.Brand>
                </Navbar>
            </div>
            <div>
                <div class="sidenav">
                    <SideNav>
                        <SideNav.Toggle />
                        <SideNav.Nav defaultSelected="about">
                            <NavItem eventKey="about">
                                <NavIcon>
                                    <Link to="/about"><i class="glyphicon glyphicon-info-sign"></i></Link>
                                </NavIcon>
                                <NavText><Link to="/about"> About </Link></NavText>
                            </NavItem>
                            <NavItem eventKey="test">
                                <NavIcon>
                                    <Link to="/test"><i class="glyphicon glyphicon-asterisk"></i></Link>
                                </NavIcon>
                                <NavText><Link to="/test"> Test </Link></NavText>
                            </NavItem>
                        </SideNav.Nav>
                    </SideNav>
                </div>
                <div class="content">
                    <div class="image">
                        <Image class ="image" src={img_cy} thumbnail fluid />
                    </div>
                    <div class="about_indiv_info">
                        <p>Chunzheng Yang
                            <br></br>Back-end Team Member</p>
                    </div>
                    <div class="about_more_info">
                        <p> <br></br> <br></br>
                            Hello! I'm Chunzheng Yang. I am glad to do this project with my teammates.
                            I hope I can learn their advantages and become better.
                            <br></br> <br></br>
                            My main programming language is Java. 
                            I love coding (Java). 
                            I love studying the latest Java technologies.
                        </p>
                    </div>
                </div>
            </div>
            </body>
        );
    }

}
