import React from 'react';
import Navbar from "react-bootstrap/Navbar";
import Image from "react-bootstrap/Image";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "bootstrap/dist/css/bootstrap.min.css";

import SideNav, {NavItem, NavIcon, NavText } from '@trendmicro/react-sidenav';
import "@trendmicro/react-sidenav/dist/react-sidenav.css";

import {Link} from 'react-router-dom'

import "./styles.css"
import img_M from "./images/mica.png"
import img_ms from "./images/maria_img.jpg"
import img_cy from "./images/cyang_img.jpg"
import img_A from "./images/anna_img.JPG"
import img_k from "./images/kk.png"


export default class About extends React.Component {
    render() {
        return (
            <body>
                <div class="sticky-top">
                    <Navbar variant="dark" sticky="top">
                    <Navbar.Brand>RefrigeratorOrganizer</Navbar.Brand>
                    </Navbar>
                </div>
                <div>
                    <div class="sidenav">
                        <SideNav>
                            <SideNav.Toggle />
                            <SideNav.Nav defaultSelected="about">
                                <NavItem eventKey="about">
                                    <NavIcon>
                                        <Link to="/about"><i class="glyphicon glyphicon-info-sign"></i></Link>
                                    </NavIcon>
                                    <NavText><Link to="/about"> About </Link></NavText>
                                </NavItem>
                                <NavItem eventKey="test">
                                    <NavIcon>
                                        <Link to="/test"><i class="glyphicon glyphicon-asterisk"></i></Link>
                                    </NavIcon>
                                    <NavText><Link to="/test"> Test </Link></NavText>
                                </NavItem>
                            </SideNav.Nav>
                        </SideNav>
                    </div>
                    <div id="main" class="content">
                        MEET OUR TEAM
                        <div class="row_cont">
                            <Container fluid>
                                <Row>
                                    <Col xs={8} md={5} lg={3}>
                                        <Link to={"/about_maria"}>
                                        <Image class ="image" src={img_ms} thumbnail fluid />
                                        </Link>
                                        <div class="about_info">
                                            <p>Maria Luciana Seljak
                                            <br></br>Team Lead</p>
                                        </div>
                                    </Col>
                                    <Col xs={8} md={5} lg={3}>
                                        <Link to="/about_micaella">
                                            <Image class ="image" src={img_M} thumbnail fluid />
                                        </Link>
                                        <div class="about_info">
                                            <p>Micaella Morales
                                            <br></br>Front-end lead</p>
                                        </div>
                                    </Col>
                                    <Col xs={8} md={5} lg={3}>
                                        <Image class ="image" src={""} thumbnail fluid />
                                        <div class="about_info">
                                            <p>NAME
                                            <br></br>Back-end Lead</p>
                                        </div>
                                    </Col>
                                    <Col xs={8} md={5} lg={3}>
                                        <Link to={"/about_katie"}>
                                          <Image class ="image" src={img_k} thumbnail fluid />
                                        </Link>
                                        <div class="about_info">
                                            <p>Katie Kirchner
                                            <br></br>Github Master</p>
                                        </div>
                                    </Col>
                                    <Col xs={8} md={5} lg={3}>
                                        <Link to="/about_anna">
                                            <Image class ="image" src={img_A} thumbnail fluid />
                                        </Link>
                                        <div class="about_info">
                                            <p>Anna Rachel Valdez
                                            <br></br>Scrum Master</p>
                                        </div>
                                    </Col>
                                    <Col xs={8} md={5} lg={3}>
                                        <Link to={"/about_chunzheng"}>
                                        <Image class ="image" src={img_cy} thumbnail fluid />
                                        </Link>
                                        <div class="about_info">
                                            <p>Chunzheng Yang
                                            <br></br>Back-end Team Member</p>
                                        </div>
                                    </Col>
                                    <Col xs={8} md={5} lg={3}>
                                        <Image class ="image" src={""} thumbnail fluid />
                                        <div class="about_info">
                                            <p>NAME
                                            <br></br>Position</p>
                                        </div>
                                    </Col>
                                </Row>
                            </Container>
                        </div>
                    </div>
                </div>
            </body>
        );
    }

}
