import React from 'react';
import Navbar from "react-bootstrap/Navbar";
import Image from "react-bootstrap/Image";
import "bootstrap/dist/css/bootstrap.min.css";

import SideNav, {NavItem, NavIcon, NavText } from '@trendmicro/react-sidenav';
import "@trendmicro/react-sidenav/dist/react-sidenav.css";

import {Link} from 'react-router-dom';

import "./styles.css";
import img_M from "./images/anna_img.JPG"

export default class About_Anna extends React.Component {
    render() {
        return (
            <body>
            <div class="sticky-top">
            <Navbar variant="dark" sticky="top">
            <Navbar.Brand>RefrigeratorOrganizer</Navbar.Brand>
            </Navbar>
            </div>
            <div>
            <div class="sidenav">
            <SideNav>
            <SideNav.Toggle />
            <SideNav.Nav defaultSelected="about">
            <NavItem eventKey="about">
            <NavIcon>
            <Link to="/about"><i class="glyphicon glyphicon-info-sign"></i></Link>
        </NavIcon>
        <NavText><Link to="/about"> About </Link></NavText>
        </NavItem>
        <NavItem eventKey="test">
            <NavIcon>
            <Link to="/test"><i class="glyphicon glyphicon-asterisk"></i></Link>
        </NavIcon>
        <NavText><Link to="/test"> Test </Link></NavText>
        </NavItem>
        </SideNav.Nav>
        </SideNav>
        </div>
        <div class="content">
            <div class="image">
            <Image class ="image" src={img_M} thumbnail fluid />
        </div>
        <div class="about_indiv_info">
            <p>Anna Rachel Valdez
        <br></br>Scrum Master</p>
        </div>
        <div class="about_more_info">
            <p> <br></br> <br></br>
        Hello! I'm a Computer Science and Psychology major student at San Francisco State University.
        As the scrum master, my goal is to make the team effective with communication
                 throughout the development of our product
        in order to ensure its successful implementation. This project would help me prepare for the real world.
        <br></br> <br></br>
        Outside of school, I introduce computer science to elementary school students.
                I enjoy coffee and korean dramas.
        </p>
        </div>
        </div>
        </div>
        </body>
    );
    }

}
